body {
  margin: 0;
  padding: 0;
  background-color: #292c33;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.textarea_container {
  border: 1px solid #fff;
  background-color: #a8b2bb;
  margin: auto; }
  .textarea_container .textarea_box {
    margin: 20px; }
    .textarea_container .textarea_box .textarea_dotBox {
      display: -webkit-flex;
      display: flex;
      background-color: #292c33; }
      .textarea_container .textarea_box .textarea_dotBox.whiteTheme {
        color: black;
        background-color: #f8f8f8; }
      .textarea_container .textarea_box .textarea_dotBox .dot {
        margin-right: 5px; }
        .textarea_container .textarea_box .textarea_dotBox .dot.red {
          color: #ed655a; }
        .textarea_container .textarea_box .textarea_dotBox .dot.yellow {
          color: #f6c150; }
        .textarea_container .textarea_box .textarea_dotBox .dot.green {
          color: #64ca57; }
        .textarea_container .textarea_box .textarea_dotBox .dot.left {
          margin-left: 5px; }
    .textarea_container .textarea_box .textarea_input {
      background-color: #292c33;
      color: #f8f8f8;
      border-radius: 0px;
      border: none;
      font-size: 14px;
      outline: none;
      padding: 7px;
      resize: none;
      overflow: auto;
      box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23); }
      .textarea_container .textarea_box .textarea_input.whiteTheme {
        color: black;
        background-color: #f8f8f8; }

.Header_text {
  color: #fff;
  text-align: center;
  font-size: 2em;
  margin-left: 12px; }

.button_common {
  background-color: #292c33;
  color: #fff;
  border: 1px solid #f8f8f8;
  padding: 12px 20px;
  margin-right: 5px;
  outline: none;
  cursor: pointer; }
  .button_common.green {
    color: #64ca57;
    border: 1px solid #64ca57; }
  .button_common:hover {
    opacity: 0.65; }
  .button_common.red {
    color: #ed655a;
    border: 1px solid #ed655a; }

.buttonBox {
  text-align: center;
  margin-top: 40px;
  margin-bottom: 20px; }
  .buttonBox .textLength {
    color: #f8f8f8; }
    .buttonBox .textLength.red {
      color: #ed655a; }
  .buttonBox .warnMessage {
    color: #ed655a; }

.git {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 50px; }

a:link {
  color: #f8f8f8;
  text-decoration: none; }

a:visited {
  color: #f8f8f8;
  text-decoration: none; }

a:hover {
  color: #f8f8f8;
  text-decoration: underline; }

a:active {
  color: #f8f8f8; }

